import HttpClient from "../http-client";
import { INVOICE_LIST, INVOICE_DONWLOAD } from '../Api'
import axios from "axios";


export const getInvoiceList = () => {
    return new HttpClient().get(INVOICE_LIST)
}

export const invoiceDownload = (invoiceId) => {
    return axios.get(`${INVOICE_DONWLOAD}/${invoiceId}`, { responseType: 'arraybuffer', headers: { 'Accept': 'application/pdf' } })
}



